import React from 'react';
import './Contador.scss';
import logoZoom from './7432215c95ccc38a.png'; 

class Contador extends React.Component {



componentDidMount() {          
// Set the date we're counting down to
var countDownDate = new Date("Jul 16, 2021 17:59:59").getTime();

// Update the count down every 1 second
var x = setInterval(function() {

  // Get today's date and time
  var now = new Date().getTime();

  // Find the distance between now and the count down date
  var distance = countDownDate - now;

  // Time calculations for days, hours, minutes and seconds
  var days = Math.floor(distance / (1000 * 60 * 60 * 24));
  var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
  var seconds = Math.floor((distance % (1000 * 60)) / 1000);

  // Display the result in the element with id="demo"
  document.getElementById("dias").innerHTML = days ;
  document.getElementById("horas").innerHTML = hours;
  document.getElementById("minutos").innerHTML = minutes;
  document.getElementById("segundos").innerHTML = seconds ;
  


  // If the count down is finished, write some text
  if (distance < 0) {
    clearInterval(x);
    document.getElementById("tiempoMarcador").innerHTML = "Tiempo Agotado!!";
  }

}, 1000);

    }


render(){
  return (
        <div className="container-fluid">
        <div className="text-center contador">
            <h3> Fecha límite de inscripción 16 de julio.</h3>
             <ul class="contiene-tiempo" id="tiempoMarcador">
                <li>
                    <h3 id="dias"></h3>
                    <p>Días</p>
                </li>
                <li>
                    <h3 id="horas"></h3>
                    <p>Horas</p>
               </li>
               <li>
                  <h3 id="minutos"></h3>
                  <p>Minutos</p>
               </li>
               <li>
                <h3 id="segundos"></h3>
                <p>Segundos</p>
               </li>
              </ul>

              <div className="viaZoom"> 
                <h2> En vivo vía  </h2>
                <img className="img-responsive" src={logoZoom}/>                
              </div>

              <div className="experiencia">
                <p> <i> Esto opinan quienes han participado de nuestros talleres. </i></p>
                <p> <i>Sumate tu también a esta experiencia </i> </p>
              </div>
              <hr/>
              <div class="loop owl-carousel owl-theme">
                <div class="item">
                    <img src="https://www.manualidadeselsultan.com/wp-content/uploads/2021/05/review03.jpeg" alt=""/>
                </div>
                <div class="item">
                    <img src="https://www.manualidadeselsultan.com/wp-content/uploads/2021/05/review02.jpeg" alt=""/>
                </div>
                <div class="item">
                    <img src="https://www.manualidadeselsultan.com/wp-content/uploads/2021/05/review01.jpeg" alt=""/>
                </div>
              </div>
        </div>
        </div>
  );
}

}

export default Contador;
