import React from 'react';
import axios from 'axios';
import './Newsletter.scss';

class Newsletter extends React.Component {

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {

        event.preventDefault();
        const data = new FormData(event.target);
                
        axios({
            method: 'POST',
            url: 'https://www.manualidadeselsultan.com/values/crm/sultan/',
            data: {
                nombre: data.get('nombre'),
                correo: data.get('correo')            
            }
        })
        .then(res => this.setState({ recipes: res.data }));

        alert("Ya estas suscrito. Gracias.");

    }


    render() {
        return (
            <div>
                <h4>SUSCRÍBETE</h4>
                <p>TE LLEGARÁ NUESTRO NEWSLETTER</p>
                <form onSubmit={this.handleSubmit}>
                    <input placeholder="Nombre" type="text" name="nombre"/>
                    <input placeholder="Correo Electrónico" type="email" name="correo"/>
                    <button>Enviar</button>
                </form>





            </div>



        ); 
    }
}

export default Newsletter;
